.card {
  background-color: hsl(218deg, 23%, 16%);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.card-body {
  width: 40rem;
  min-height: 20vh;
  background-color: hsl(217deg, 19%, 24%);
  padding: 3rem 5rem;
  border-radius: 2rem;
  display: flex;
  gap: 2rem;
  flex-direction: column;
  align-items: center;
  overflow: visible;
}
.card-body .divider {
  position: relative;
  top: 2rem;
}
.card-body .dice {
  width: 2.3rem;
  height: 2.3rem;
  background-color: hsl(150deg, 100%, 66%);
  border-radius: 5rem;
  padding: 1.8rem 1.8rem;
  display: flex;
  position: relative;
  top: 5rem;
}
.card-body .dice:hover {
  box-shadow: 0px 0px 5rem hsl(150deg, 100%, 66%);
}
.card-body .dice img {
  width: 2.3rem;
  height: 2.3rem;
}

h1 {
  font-size: 1.2rem;
  color: hsl(150deg, 100%, 66%);
  letter-spacing: 2px;
}

P {
  font-size: 2.5rem;
  line-height: 3rem;
  text-align: center;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

html {
  font-size: 62.5%;
  color: hsl(193deg, 38%, 86%);
  font-family: "https://fonts.google.com/specimen/Manrope";
  font-weight: 800;
}

::selection {
  color: hsl(150deg, 100%, 66%);
}